<template>
  <view class="container">
    <!-- 顶部搜索 -->
    <view class="header" id="header">
      <u-search
        v-model="searchKeyword"
        placeholder="请输入要搜索的店铺名"
        :show-action="false"
        :clearabled="true"
        bgColor="#f5f5f5"
        height="70rpx"
        @search="resetLoad"
        @clear="resetLoad"
      />
    </view>

    <view class="main-content">
      <!-- 左侧分类 -->
      <view class="category-sidebar">
        <view
          class="category-item"
          :class="{ active: currentCategory === item.id }"
          v-for="item in categories"
          :key="'c-' + item.id"
          @click="selectCategory(item)"
        >
          <u-text :text="item.name" align="center" />
        </view>
      </view>

      <!-- 右侧可滚动店铺列表 -->
      <view class="store-list-wrapper">
        <scroll-view
          class="store-scroll"
          scroll-y
          :style="{ height: scrollHeight }"
          @scrolltolower="onScrollToLower"
        >
          <view
            class="store-card"
            v-for="store in stores"
            :key="'s-' + store.id"
            @click="viewStore(store)"
          >
            <image :src="store.logo" class="store-logo-img" mode="aspectFit" />
            <view class="store-info">
              <u-text class="store-name" :text="store.name" />
              <u-text class="store-location" :text="'位置:' + store.location" />
              <u-text class="store-desc" :text="store.description" />
            </view>
          </view>

          <!-- 空态 -->
          <u-empty v-if="!stores.length && !isLoading" mode="list" marginTop="120rpx" />

          <!-- 加载更多 -->
          <u-loadmore
            v-if="stores.length"
            :status="loadStatus"
            loading-text="努力加载中..."
            loadmore-text="上拉加载更多"
            nomore-text="没有更多了"
            margin-top="30"
            margin-bottom="30"
          />
        </scroll-view>
      </view>
    </view>

    <!-- 右下角楼层导航 -->
    <view class="floor-nav">
      <view class="floor-nav-container">
        <view class="floor-nav-item arrow-item" @click="scrollTop">
          <u-icon name="arrow-up" size="20" color="#001733" />
        </view>
        <scroll-view class="floor-nav-scroll" scroll-y>
          <view
            class="floor-nav-item"
            :class="{ active: currentFloorId === floor.id }"
            v-for="floor in floors"
            :key="'f-' + floor.id"
            @click="selectFloor(floor)"
          >
            <u-text :text="floor.floorItemName" align="center" />
          </view>
        </scroll-view>
        <view class="floor-nav-item arrow-item" @click="scrollBottom">
          <u-icon name="arrow-down" size="20" color="#001733" />
        </view>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      searchKeyword: '',
      currentCategory: 'all',
      currentFloorId: 0,
      categories: [{ id: 'all', name: '全部' }],
      floors: [{ id: 0, floorItemName: '全部' }],
      stores: [],
      plazaId: null,

      scrollHeight: '100vh',

      /* 分页 & 状态 */
      pageIndex: 1,
      pageSize: 10,
      hasMore: true,
      loadStatus: 'loadmore',
      isLoading: false
    };
  },
  async onLoad() {
    const plaza = uni.getStorageSync('selectedPlaza');
    if (!plaza) {
      uni.showToast({ title: '请先选择广场', icon: 'none' });
      return uni.navigateTo({ url: '/pages/map/mapTest' });
    }
    this.plazaId = plaza.id;
    await Promise.all([this.loadFloors(), this.loadCategories()]);
    this.calcScrollHeight();   // 计算右侧滚动高度
    this.resetLoad();          // 首次加载
  },
  onShow() {
    const plaza = uni.getStorageSync('selectedPlaza');
    if (!plaza) {
      uni.showToast({ title: '请先选择广场', icon: 'none' });
      return uni.navigateTo({ url: '/pages/map/mapTest' });
    }
    if (plaza.id !== this.plazaId) {
      this.plazaId = plaza.id;
      this.searchKeyword = '';
      this.currentCategory = 'all';
      this.currentFloorId = 0;
      this.resetLoad();
    }
  },
  methods: {
    /* 计算右侧 scroll-view 高度 = 屏幕高 - header - 安全区 */
    calcScrollHeight() {
      const query = uni.createSelectorQuery().in(this);
      query.select('#header').boundingClientRect();
      query.exec(res => {
        const headerH = res[0]?.height || 88;
        this.scrollHeight = `calc(100vh - ${headerH}px - var(--window-top))`;
      });
    },
    async loadFloors() {
      const res = await uni.request({ url: `/api/plaza/${this.plazaId}/floors` });
      this.floors = [{ id: 0, floorItemName: '全部' }, ...res.data];
    },
    async loadCategories() {
      const res = await uni.request({ url: '/api/store/storetypes' });
      this.categories = [
        { id: 'all', name: '全部' },
        ...res.data.map(t => ({ id: t.id.toString(), name: t.name }))
      ];
    },
    /* 统一拉数据：reset=true 表示重新搜索/切换筛选项 */
    async loadStores(reset = false) {
      if (reset) {
        this.pageIndex = 1;
        this.hasMore = true;
        this.stores = [];
      }
      if (!this.hasMore) return;

      this.isLoading = true;
      this.loadStatus = 'loading';

      const params = {
        plazaId: this.plazaId,
        pageIndex: this.pageIndex,
        pageSize: this.pageSize
      };
      if (this.currentFloorId !== 0) params.floorId = this.currentFloorId;
      if (this.currentCategory !== 'all') params.storeTypeId = this.currentCategory;
      if (this.searchKeyword) params.keyword = this.searchKeyword;

      try {
        const res = await uni.request({ url: '/api/store/stores', data: params });
        const list = res.data || [];

        /* 前端去重：只保留新 id */
        const existIds = new Set(this.stores.map(s => s.id));
        const newList = list.filter(item => !existIds.has(item.id));
        this.stores.push(...newList);

        this.hasMore = newList.length === this.pageSize;
        this.loadStatus = this.hasMore ? 'loadmore' : 'nomore';
      } finally {
        this.isLoading = false;
      }
    },
    /* scroll-view 触底事件 */
    onScrollToLower() {
      if (this.loadStatus !== 'loadmore') return;
      this.pageIndex += 1;
      this.loadStores();
    },
    /* 筛选项变化 / 搜索 -> 重置到第一页 */
    selectCategory(item) {
      this.currentCategory = item.id;
      this.resetLoad();
    },
    selectFloor(floor) {
      this.currentFloorId = floor.id;
      this.resetLoad();
    },
    resetLoad() {
      this.loadStores(true);
    },
    viewStore(store) {
      uni.navigateTo({ url: `/pages/store/storedetailTest?id=${store.id}` });
    },
    scrollTop() {
      uni.pageScrollTo({ scrollTop: 0, duration: 300 });
    },
    scrollBottom() {
      uni.pageScrollTo({ scrollTop: 99999, duration: 300 });
    }
  }
};
</script>

